<template>
  <div>
    <b-modal
      id="productModal"
      size="xl"
      title=""
      :header-bg-variant="headerBgVariant"
      :header-text-variant="headerTextVariant"
      :body-bg-variant="bodyBgVariant"
      :body-text-variant="bodyTextVariant"
      :footer-bg-variant="footerBgVariant"
      :footer-text-variant="footerTextVariant"
    >
      <b-container fluid>
        <div class="modal-body">
          <b-row>
            <b-col sm="4">
              <div class="form-group" v-for=" i in 3" :key="i + 'img'">
                <label :for="'img' + i"></label>
                <input
                type="text"
                :id="'img' + i"
                class="form-control"
                placeholder=""
                v-model="tempProduct.imageUrl[i - 1]"
                />
              </div>
              <div class="form-group">
                <label for="customFile">
                   
                  <i
                    class="fas fa-spinner fa-spin"
                    v-if="status.fileUploading"
                  ></i>
                </label>
                <input
                  type="file"
                  ref="file"
                  id="customFile"
                  class="form-control"
                  @change="uploadFile"
                  />
              </div>
              <img alt class="img-fluid" :src="tempProduct.imageUrl[0]"/>
            </b-col>
            <b-col sm="8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  type="text"
                  id="title"
                  class="form-control"
                  placeholder=""
                  v-model="tempProduct.title"
                  required />
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    type="text"
                    id="category"
                    class="form-control"
                    placeholder=""
                    v-model="tempProduct.category"
                    required
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input
                    type="text"
                    id="unit"
                    class="form-control"
                    v-model="tempProduct.unit"
                    placeholder="" />
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    type="text"
                    id="origin_price"
                    class="form-control"
                    v-model="tempProduct.origin_price"
                    placeholder="" />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    type="text"
                    id="price"
                    class="form-control"
                    v-model="tempProduct.price"
                    placeholder=""/>
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="game_player"></label>
                  <input
                    type="text"
                    id="game_player"
                    class="form-control"
                    v-model="tempProduct.options.game_player"
                    placeholder="" />
                </div>
                <div class="form-group col-md-6">
                  <label for="game_time"></label>
                  <input
                    type="text"
                    id="game_time"
                    class="form-control"
                    v-model="tempProduct.options.game_time"
                    placeholder="" />
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="player_age"></label>
                  <input
                    type="text"
                    id="player_age"
                    class="form-control"
                    v-model="tempProduct.options.player_age"
                    placeholder="" />
                </div>
                <div class="form-group col-md-6">
                  <label for="game_rating">BGG </label>
                  <input
                    type="text"
                    id="game_rating"
                    class="form-control"
                    v-model="tempProduct.options.game_rating"
                    placeholder=" BGG " />
                </div>
              </div>
              <hr />
              <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  class="form-control"
                  type="text"
                  placeholder=""
                  v-model="tempProduct.description"
                  required
                ></textarea>
              </div>

              <div class="form-group">
                <label for="content"></label>
                <vue-editor
                  id="content"
                  placeholder=""
                  v-model="tempProduct.content"
                  required
                />
              </div>

              <div class="form-group">
                <div class="form-check">
                  <input
                    type="checkbox"
                    id="enabled"
                    v-model="tempProduct.enabled"
                    class="form-check-input" />
                  <label for="enabled" class="form-check-label"></label>
                </div>
              </div>
            </b-col>
          </b-row>
        </div>
      </b-container>
      <template v-slot:modal-footer>
        <b-button
          type="button"
          variant="outline-secondary"
          @click="$bvModal.hide('productModal')">
        </b-button>
        <b-button
          type="button"
          variant="outline-primary"
          @click="updateProduct">
        </b-button>
      </template>
    </b-modal>
  </div>
</template>

<script>
import Toast from '@/plugins/Toast';
import { VueEditor } from 'vue2-editor/dist/vue2-editor.core';

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      //  imageUrl
      tempProduct: {
        imageUrl: [],
        options: {},
      },
      //  icon
      status: {
        fileUploading: false,
      },
      show: false,
      variants: [
        'light',
        'dark',
      ],
      headerBgVariant: 'dark',
      headerTextVariant: 'light',
      bodyBgVariant: 'light',
      bodyTextVariant: 'dark',
      footerBgVariant: '',
      footerTextVariant: 'dark',
    };
  },
  // 
  props: ['isNew'],
  methods: {
    //  id
    getProduct(id) {
      const loader = this.$loading.show();
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.axios
        .get(api)
        .then((res) => {
          this.tempProduct = res.data.data;
          loader.hide();
          this.$bvModal.show('productModal');
          if (!this.tempProduct.options) {
            this.tempProduct.options = {};
          } else {
            this.options = this.tempProduct.options;
          }
        })
        .catch(() => {
          Toast.fire({
            title: '',
            icon: 'error',
          });
          loader.hide();
        });
    },
    // 
    updateProduct() {
      const loader = this.$loading.show();
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product`;
      let httpMethod = 'post';
      //  api
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }
      this.axios[httpMethod](api, this.tempProduct)
        .then(() => {
          this.$emit('update'); // 
          this.$bvModal.hide('productModal');
          loader.hide();
          Toast.fire({
            title: '',
            icon: 'success',
          });
        })
        .catch(() => {
          this.$bvModal.hide('productModal');
          loader.hide();
          Toast.fire({
            title: '',
            icon: 'error',
          });
        });
    },
    // 
    uploadFile() {
      const loader = this.$loading.show();
      const uploadedFile = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append('file', uploadedFile);
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage`;
      this.status.fileUploading = true;
      this.axios
        .post(url, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((res) => {
          this.status.fileUploading = false;
          if (res.status === 200) {
            this.tempProduct.imageUrl.push(res.data.data.path);
            loader.hide();
          }
        })
        .catch(() => {
          loader.hide();
          Toast.fire({
            title: ' 2 MB',
            icon: 'error',
          });
          this.status.fileUploading = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
@import "~vue2-editor/dist/vue2-editor.css";

