<template>
  <div>
    <b-modal
      id="delProduct"
      v-model="show"
      title=""
      :header-bg-variant="headerBgVariant"
      :header-text-variant="headerTextVariant"
      :body-bg-variant="bodyBgVariant"
      :body-text-variant="bodyTextVariant"
      :footer-bg-variant="footerBgVariant"
      :footer-text-variant="footerTextVariant"
    >
      <b-container fluid>
        <p>
          
          {{ tempProduct.title }}
          ()?
        </p>
      </b-container>
      <template v-slot:modal-footer>
        <b-button type="button" variant="danger" @click="delProduct"></b-button>
        <b-button
          type="button"
          variant="outline-secondary"
          @click="$bvModal.hide('delProduct')"
        ></b-button>
      </template>
    </b-modal>
  </div>
</template>

<script>
import Toast from '@/plugins/Toast';

export default {
  data() {
    return {
      show: false,
      variants: [
        'primary',
        'secondary',
        'success',
        'warning',
        'danger',
        'info',
        'light',
        'dark',
      ],
      headerBgVariant: 'danger',
      headerTextVariant: 'light',
      bodyBgVariant: 'light',
      bodyTextVariant: 'dark',
      footerBgVariant: 'light',
      footerTextVariant: 'dark',
    };
  },
  props: ['tempProduct'],
  methods: {
    delProduct() {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
      this.axios.delete(url).then(() => {
        this.$bvModal.hide('delProduct'); //  modal
        this.$emit('update'); // 
        Toast.fire({
          title: '',
          icon: 'success',
        });
      });
    },
  },
};
</script>
